		// 		qstn5Data = cookiesState.qstn5Data,
		// 		steps = cookiesState.steps;
		// 		q1SelectedValue = cookiesState.q1SelectedValue;
		// 		//selectedComparedProduct = cookiesState.selectedComparedProduct;
