		$('.btn-compareprinter').on('click', function() {
			state = {
				qstn1Data,
				qstn2Data,
				qstn3Data,
				qstn4Data,
				qstn5Data,
				steps: steps,
				q1SelectedValue,
				selectedComparedProduct,
				selectedFeatures,
				finalData
			}

			let stateStr = JSON.stringify(state);
			let idStr = selectedComparedProduct.toString();
			localStorage.setItem('id', JSON.stringify(idStr))
			localStorage.setItem('state', JSON.stringify(stateStr));

			// Cookies.set('id', JSON.stringify(idStr), { expires: 7 });
			// Cookies.set('state', JSON.stringify(stateStr), { expires: 7 });
			window.location.href = `${$('.product-finder-section').data('url')}?id=${idStr}`;
			// window.location.href = "/product-finder-compare?id=" + idStr;

		});

		// back to prev question
		$('.product-finder__footer--back button').on('click', function(e){
			e.preventDefault();

			$('.product-finder__empty').hide();

			steps--;
			$(`[data-stepno=${steps}]`).addClass('is-active');

			$('.product-finder__question input[type="radio"]:checked').prop('checked', false);
			$('.product-finder__question input[type="checkbox"]:checked').prop('checked', false);


			$(`.product-finder__question.is-active`).fadeOut(function(){
				$(this).removeClass('is-active');
				$('.steps-list .is-active').removeClass('is-active');

				$(`.steps-list [data-stepno=${steps}]`).addClass('is-active');
				$(`.product-finder__question[data-stepno=${steps}]`).addClass('is-active');
				$(`.product-finder__question[data-stepno=${steps}]`).delay(400).fadeIn();
			});

			$('.product-finder-results.is-active').fadeOut(function(){
				$(this).removeClass('is-active');
			});

			hidePrinterListing();

			// reset selected features from page 5
			selectedFeatures = [];

			switch(steps){
				case 1:
					footerResult(printers);
					$('.product-finder__footer--back').hide();
					$('.product-finder__footer--right').hide();
					break;
				case 2:
					footerResult(qstn1Data);
					break;
				case 3:
					footerResult(qstn2Data);
					break;
				case 4:
					footerResult(qstn3Data);
					checkQ1ToDisplayQ4();
					$('.product-finder__footer .btn-findprinter').removeClass('is-shown');
					checkQ1ToDisplayQ4();
					hideQ4NoResultOptions();
					break;
				case 5:
					footerResult(qstn4Data);
					$('.product-finder__footer--right .btn-compareprinter').hide();
					if($(`.question5 input[name="feature"]:checked`).length > 0){
						$('.product-finder__footer--right .btn-findprinter').addClass('is-shown');
					}
					break;
				case 6:
					footerResult(qstn5Data);

					if ( qstn5Data.length <= 0) {
						$('.product-finder__empty').show();
						$('.product-finder-results').removeClass('is-active');
						$('.product-finder-results').hide();
						// $('.finder-results__desc h2').html(`<span>${finalData.length}</span> printer fit your needs`);
					}
					break;
			}
		});

		function processQuestion(steps) {
			// set active step and questions
			$(`[data-stepno=${steps}]`).addClass('is-active');

			$(`.product-finder__question.is-active`).fadeOut(function(){
				$(this).removeClass('is-active');
				$('.steps-list .is-active').removeClass('is-active');
				setTimeout(function() {
					$(`.product-finder__question[data-stepno=${steps}]`).fadeIn();
				}, 100);

				$(`.steps-list [data-stepno=${steps}]`).addClass('is-active');
				$(`.product-finder__question[data-stepno=${steps}]`).addClass('is-active');
				$(`.product-finder__question[data-stepno=${steps}]`).delay(400).fadeIn();
			});

			$('.product-finder__footer--back').show();
			$('.product-finder__footer--right').show();
		}

		function showPrinterListing(results) {
			let $productItem = $('.product-item'),
				itemTitle = $('h3', $productItem),
				itemDesc = $('p', $productItem);

			if ($productItem.length) {
				itemTitle.matchHeight();
				itemDesc.matchHeight();
			}

			if ( `${results.length}` <= 0) {
				$(`.product-finder__question.is-active`).fadeOut(function(){
					$(this).removeClass('is-active');
				});
				$('.product-finder__footer--results').hide();
				$('.product-finder__steps').hide();
			} else {
				$('.product-finder__steps').hide();
				$('.product-finder__footer--results').hide();
				$('.product-finder-results').delay(400).fadeIn();
				$('.product-finder-results').addClass('is-active');

				$(`.product-finder__question.is-active`).fadeOut(function(){
					$(this).removeClass('is-active');
				});
			}
		}

		function footerResult(results) {
			// if ( results.length <= 1) {
			// 	// $('.finder-results__desc span').text(qstn5Data.length);
			// 	$('.product-finder__footer--results p').html(`<span>${results.length}</span> printer fit your needs`);
			// } else {
			// 	$('.product-finder__footer--results p').html(`<span>${results.length}</span> printers fit your needs`);

			// }
			let resultTxt = $('.product-finder__footer--results span.txt').data('results');
				$('.product-finder__footer--results span.num').html(` ${results.length}`);
				$('.product-finder__footer--results span.txt').html(` ${resultTxt}`);
		}

		function productChecked() {
			let $productList = $('.product-finder-results'),
				$checkbox = $('.product-item input', $productList),
				$compareBtn = $('.btn-compareprinter');

			$checkbox.map((i, ele) => {
				let $this = $(ele),
					$checked = $('.product-item input[type=checkbox]:checked');

				if (!$checked.length) {
					$compareBtn.attr("disabled", true);
				} else {
					$compareBtn.attr("disabled", false);
				}

				$this.on('change', function() {
					let $checkbox = $('.product-item input[type=checkbox]'),
						$checked = $('.product-item input[type=checkbox]:checked');

					$compareBtn.attr("disabled", false);

					if ($checked.length >= 4) {
						$('.alert-compare').removeClass('hidden');
					} else {
						$('.alert-compare').addClass('hidden');
					}

					if ($checked.length >= 5) {
						$this.prop('checked', false);
						let alertTxt = $('.product-finder-results .alert-compare').data('text');
						alert(`${alertTxt}`);
						$('.finder-results__desc .alert-compare').removeClass('hidden');
						$('.finder-results__desc .alert-compare').html('alertTxt');
						// $('.finder-results__desc').css('color', '#d92231');
					}

					// push selected product into array
					let checked = $this.val();
					if ($this.is(':checked')) {
						selectedComparedProduct.push(checked);
					} else {
						selectedComparedProduct.splice($.inArray(checked, selectedComparedProduct), 1);
					}

					if(selectedComparedProduct.length > 0){
						$('.product-finder__footer--right .btn-compareprinter').show();
					} else {
						$('.product-finder__footer--right .btn-compareprinter').hide();
					}

					// console.log('selectedComparedProduct', selectedComparedProduct);
				});
			});
		}

		function hidePrinterListing() {
			$('.product-finder__footer--results').show();
			$('.product-finder__steps').show();
			$('.product-finder-results').fadeOut(function() {
				$(this).removeClass('is-active');
			});
		}

		function noPrinter(qstnData) {
			if ( `${qstnData}`.length <= 0 ){
				$('.product-finder__empty').show();
				$('.product-finder__footer--right .btn-compareprinter').hide();
				$('.product-finder__footer--right .btn-findprinter').removeClass('is-shown');
				$('.product-finder__footer--results').hide();
				$('.product-finder__footer--back').hide();

				$(`.product-finder__question.is-active`).fadeOut(function(){
					$(this).removeClass('is-active');
					$('.steps-list .is-active').removeClass('is-active');

					$('.product-finder__steps').hide();
				});

				//$('.product-finder__footer--back').hide();

				steps;
			}
		}

