'use strict';

import $ from 'jquery';
import 'jquery-match-height';
import enquire from 'enquire.js';
import 'slick-carousel';

import CompareItem from '../../molecules/compare-item/compare-item';

export default class ProductFinderCompare {
	constructor(config) {
		const { breakpoints } = config;

		new CompareItem(breakpoints);

		let $compareWrap = $('.product-finder-compare'),
			row1 = $('.specrow-1', $compareWrap),
			row2 = $('.specrow-2', $compareWrap),
			row3 = $('.specrow-3', $compareWrap),
			row4 = $('.specrow-4', $compareWrap),
			row5 = $('.specrow-5', $compareWrap),
			row6 = $('.specrow-6', $compareWrap),
			row7 = $('.specrow-7', $compareWrap),
			row8 = $('.specrow-8', $compareWrap),
			row9 = $('.specrow-9', $compareWrap),
			row10 = $('.specrow-10', $compareWrap),
			row11 = $('.specrow-11', $compareWrap),
			row12 = $('.specrow-12', $compareWrap),
			row13 = $('.specrow-13', $compareWrap),
			row14 = $('.specrow-14', $compareWrap),
			row15 = $('.specrow-15', $compareWrap),
			row16 = $('.specrow-16', $compareWrap),
			rowlast = $('.specrow-last', $compareWrap),
			row1Title = $('h3', row1),
			row1Desc = $('p', row1);

		if ($compareWrap.length) {
			row1.matchHeight();
			row2.matchHeight();
			row3.matchHeight();
			row4.matchHeight();
			row5.matchHeight();
			row6.matchHeight();
			row7.matchHeight();
			row8.matchHeight();
			row9.matchHeight();
			row10.matchHeight();
			row11.matchHeight();
			row12.matchHeight();
			row13.matchHeight();
			row14.matchHeight();
			row15.matchHeight();
			row16.matchHeight();
			rowlast.matchHeight();
			row1Title.matchHeight();
			row1Desc.matchHeight();
		}

		$('.compare__footer--back button').on('click', function(e){
			e.preventDefault();
			window.location.href = $(this).attr('href');
		});

		$('.compare__footer--startover button').on('click', function(e){
			e.preventDefault();
			localStorage.removeItem("id");
			localStorage.removeItem("state");
			localStorage.removeItem("date");
			window.location.href = $(this).attr('href');
		});


		
		
	}
}
