'use strict';

import $ from 'jquery';
import 'jquery-match-height';

export default class ProductItem {
	constructor() {
		let $productItem = $('.product-item--wrap');
		let itemImg = $('.product-item__img', $productItem),
			itemTitle = $('h3', $productItem),
			itemDesc = $('p', $productItem);

		if ($productItem.length) {
			$productItem.matchHeight();
			itemImg.matchHeight();
			itemTitle.matchHeight();
			itemDesc.matchHeight();
		}
		
	}
}
